// @import 'reset.css';
* {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

.page-header {
    padding-top: 40px;
    padding-bottom: 0;
    text-align: center;
    background: #17408B;
    color: white;
    font-size: 24px;
}

.chartMenu {
    width: 100vw;
    height: 40px;
    background: #C9082A;
    color: #C9082A;
}
.chartMenu p {
    padding: 10px;
    font-size: 20px;
}
.chartCard {
    width: 100vw;
    height: calc(100vh - 40px);
    background: #17408B;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.chartBox {
    width: 700px;
    padding: 20px;
    border-radius: 20px;
    border-color: #C9082A;
    background: white;
}

.toggles {
    width: 700px;
    background: #17408B;
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-btn {
    background-color: #C9082A;
    border-radius: 15px;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    font-size: 16px;
    cursor: pointer;
}

.modal-bg {
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s, opacity 0.5s;
}

.bg-active {
    visibility: visible;
    opacity: 1;
}

.modal {
    position: relative;
    background-color: white;
    width: 40%;
    height: 75%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}

.modal button {
    padding: 10px 30px;
    background-color: #17408B;
    color: white;
    border: none;
    font-size: 14px;
    cursor: pointer;
    text-align: center;
    border-radius: 10px;
}

.modal-close {
    position: absolute;
    top: 10px;
    right: 10px;
    font-weight: bold;
    cursor: pointer;
}

.select-chart-params {
    position: relative;
    background-color: white;
    width: 40%;
    height: 75%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}

.input-selector {
    outline: none;
    border: none;
}

.stats-label {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.year-selectors {
    width: max-content;
    position: relative;
}

.player-selector {
    width: max-content;
    position: relative;
}

.select-field {
    border: 1px solid black;
}

.select-field,.player,.list {
    width: 100%;
    background-color: white;
    padding: 0.3rem;
}

.list {
    box-shadow: 0 30px 60px rgb(0, 0, 0, 0.2);
    display: none;
}

.down-arrow {
    font-size: 1.2rem;
    display: inline-block;
    cursor: pointer;
}

.player {
    display: block;
    padding-left: 0;
}

.player:hover {
    background-color: aliceblue;
}

.show-dropdown {
    display: block;
    max-height: 200px;
    max-width: 175px;
    overflow-y: scroll;
}

.rotate180 {
    transform: rotate(180deg);
}